import C from '../constants';


let m;
let topRatedMovies;

export default (state={movies:[], latestMovies:null, topRatedMovies:null }, action) => {
  const { movies, query, search } = action;

  switch (action.type) {
    case C.GET_MOVIES:
      if(movies.total_results === 0){
        return {movies: movies, message:'Not Found', topRatedMovies}
      }else{
        m = movies;
        return {movies: movies, message: 'Found', query, search, topRatedMovies}
      }
    case C.GET_LATEST_MOVIES:
      return { movies:m, latestMovies: movies }
    case C.GET_TOP_RATED_MOVIES:
      topRatedMovies = movies;
      if(movies.total_results === 0){
        return {movies: m, topRatedMovies: movies}
      }else{
        return {movies: m, topRatedMovies: movies}
      }
    case C.SORT_MOVIES_BY_A_TO_Z:
        state.movies.results.sort(function(a,b) { return a.title.localeCompare(b.title)});
      return {movies: m, topRatedMovies: movies}
    case C.SORT_MOVIES_BY_Z_TO_A:
        state.movies.results.sort(function(a,b) { return b.title.localeCompare(a.title)});
      return {movies: m, topRatedMovies: movies}
    case C.SORT_MOVIES_BY_RATE:
        state.movies.results.sort(function(a,b) { return parseFloat(b.vote_average) - parseFloat(a.vote_average)});
      return {movies: m, topRatedMovies: movies}
    default:
      return state;
  }
}



