import C from '../constants';

let tv;
let tvTopRatedTV;

export default (state={tvShows:[], tvTopRatedTV:[]}, action) => {
  const { tvShows, query, search } = action;

  switch (action.type) {
    case C.GET_TV_SHOWS:
      if(tvShows.total_results === 0){
        return {tvShows: tvShows, message:'Not Found', tvTopRatedTV}
      }else{
        tv = tvShows;
        return {tvShows: tvShows, message: 'Found', query, search, tvTopRatedTV}
      }
    case C.GET_TOP_RATED_TV_SHOWS:
      tvTopRatedTV = tvShows;
      if(tvShows.total_results === 0){
        return {tvShows: tv, message1:'Not Found', tvTopRatedTV}
      }else{
        tv = tvShows;
        return {tvShows: tv, message1: 'Found', query, search, tvTopRatedTV}
      }
    case C.GET_ON_AIR_TV_SHOWS:
      if(tvShows.total_results === 0){
        return {tvShows: tvShows, message:'Not Found', tvTopRatedTV}
      }else{
        tv = tvShows;
        return {tvShows: tvShows, message: 'Found', query, search, tvTopRatedTV}
      }
    case C.SORT_TVSHOWS_BY_A_TO_Z:
        state.tvShows.results.sort(function(a,b) { return a.name.localeCompare(b.name)});
      return {tvShows: tv, tvTopRatedTV}
    case C.SORT_TVSHOWS_BY_Z_TO_A:
        state.tvShows.results.sort(function(a,b) { return b.name.localeCompare(a.name)});
      return {tvShows: tv, tvTopRatedTV}
    case C.SORT_TVSHOWS_BY_RATE:
        state.tvShows.results.sort(function(a,b) { return Number(b.vote_average) - Number(a.vote_average)});
      return {tvShows: tv, tvTopRatedTV}
    default:
      return state;
  }
}



